nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 10vh;
  background: rgb(73, 79, 82);
  color: white;
}

.nav-links {
  width: 50%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none;
  height: 5rem;
}
nav > *, .nav-links > * {
  color: white;
}

.nav-links > * {
  transition: all 0.1s ease-in-out;
}

.cart {
  border: 1px solid white;
  padding: 1rem;
  border-radius: 50px;
}

.homepageDescription {
  display: grid;
  grid-template-rows: repeat(3, 1fr);
  height: 60%;
  row-gap: 5rem;

}
.homepageDescription > button {
  height: 3rem;
  width: 10rem;
}

.homepageImage > img {
  height: 40rem;
  width: 30rem;
  border-radius: 10px;
}


.homepage {
display: grid;
grid-template-columns: 1fr 1fr;
width: 80%;
margin-top: 3rem;
margin-right: 2rem;
margin-left: 10rem;
}

a {
  text-decoration: none;
}

.nav-links > *:hover {
  transform: scale(1.1);
}

.product {
  list-style-type: none;
  border: 1px solid black;
}

.productList {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  row-gap: 3rem;
  column-gap: 2rem;
  margin-right: 2rem;
}
.product > img {
  height: 19rem;
  width: 18rem;
}

.product {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.product > * {
  padding: 0.5rem;
}
.product > button {
  margin-bottom: 1rem;
}